function sliderCoaches() {
    var swiper = new Swiper('.coaches .swiper', {
        slidesPerView: '1',
        spaceBetween: 100,
        mousewheel: {
            sensitivity: 1,
        },
        breakpoints: {
            320: {
                 mousewheel: false,
            },
            577: {
                 mousewheel: {
                    sensitivity: 1,
                },
            },
        }
    })
}

function sliderStudents() {
    var swiper = new Swiper('.students .swiper', {
        slidesPerView: '2',
        spaceBetween: 30,
        speed: 500,
        navigation: {
            nextEl: '.students .swiper-button-next',
            prevEl: '.students .swiper-button-prev',
        },
        pagination: {
            el: ".students .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
                spaceBetween: 20,
            },
            401: {
                slidesPerView: '2',
                spaceBetween: 20,
            },
            993: {
                slidesPerView: '2',
                spaceBetween: 30,
            },
        }
    })
}

function sliderFeedbacks() {
    var swiper = new Swiper('.feedbacks .swiper', {
        slidesPerView: '3',
        spaceBetween: 20,
        navigation: {
            nextEl: '.feedbacks .swiper-button-next',
            prevEl: '.feedbacks .swiper-button-prev',
        },
        pagination: {
            el: ".feedbacks .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
            },
            577: {
                slidesPerView: '2',
            },
            769: {
                slidesPerView: '1',
            },
            993: {
                slidesPerView: '2',
            },
            1301: {
                slidesPerView: '3',
            },
        }
    })
}

function sliderVideo() {
    var swiper = new Swiper('.video .swiper', {
        slidesPerView: 'auto',
        spaceBetween: 20,
        navigation: {
            nextEl: '.video .swiper-button-next',
            prevEl: '.video .swiper-button-prev',
        },
        pagination: {
            el: ".video .swiper-pagination",
            clickable: true,
        },
    })
}

function sliderCertificates() {
    var swiper = new Swiper('.certificates .swiper', {
        slidesPerView: '4',
        spaceBetween: 40,
        navigation: {
            nextEl: '.certificates .swiper-button-next',
            prevEl: '.certificates .swiper-button-prev',
        },
        pagination: {
            el: ".certificates .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
            },
            401: {
                slidesPerView: '2',
            },
            769: {
                slidesPerView: '3',
            },
            993: {
                slidesPerView: '4',
            },
        }
    })
}


$(document).ready(function() {
    sliderCoaches();
    sliderStudents();
    sliderFeedbacks();
    sliderVideo();
    sliderCertificates();

    //Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('body').toggleClass('fixed-body')
    })

    let headerNav = $('.header')
    let scrollPrev = 0
    $(window).scroll(function () {
    let scrolled = $(window).scrollTop()

    if (scrolled > 200) {
        if (scrolled < scrollPrev - 10) {
            headerNav.addClass('header_fixed')
        }

        if (scrolled > scrollPrev) {
            headerNav.removeClass('header_fixed')
        }
    } else {
        headerNav.removeClass('header_fixed')
    }
    scrollPrev = scrolled
})

    //Info-card
    $('.info-card').hover(function () {
        $(this).siblings(".info-card").removeClass("info-card--active")
        $(this).addClass("info-card--active")
    })


    //Accordion
    $(".questions__item-show").click((function () {
        $(this).toggleClass("questions__item-show--active")
        $(this).siblings(".questions__item-hidden").slideToggle()
    }));

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs").find(".tabs-content").removeClass("tabs-content--active")
        $(this).parents(".tabs").find(`.tabs-content[data-content-path="${path}"]`).addClass("tabs-content--active")
    })

    //Estimate
    $('.estimate__group .estimate__btn').click(function () {
        $(this).parents('.estimate__group').css('display', 'none')
        $('.estimate__group2').css('display', 'flex')
    })

    // modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})

